<template>
  <div>
    <Table :columns="columns7" :data="Storages">
      <template slot-scope="{ row, index }" slot="indexs">{{index+1}}</template>
      <template slot-scope="{ row, index }" slot="action">
        <Icon
          @click="addrow(index)"
          type="ios-add-circle-outline"
          style="margin-right:5px;font-size:20px;font-width:600"
        />
        <Icon
          @click="delrow(index)"
          type="ios-trash-outline"
          style="font-size:20px;font-width:600"
        />
      </template>
      <template slot-scope="{ row, index }" slot="Place">
        <Select
          v-model="row.originID"
          :transfer="true"
          filterable
          :label-in-value="true"
          @on-change="changoriginID($event,row)"
        >
          <Option v-for="item in Conveyingplace" :value="item.CorPlace" :key="item.ID">{{item.Text}}</Option>
        </Select>
      </template>
      <template slot-scope="{ row, index }" slot="ShelveID" v-if="status==1">
        <Select
          size="default"
          v-model="row.ShelveID"
          filterable
          :transfer="true"
          :label-in-value="true"
          @on-change="changConveyingplace($event,row)"
        >
          <Option
            v-for="(item,index) in Storehouselistarr"
            :value="item.ShelveID"
            :label="item.ShelveID"
            :key="index"
          >
            <span>{{item.ShelveID}}</span>
            <span style="float:right;color:#ccc">{{item.TotalPackage}}</span>
          </Option>
        </Select>
      </template>
      <template slot-scope="{ row, index }" slot="ShelveID" v-if="status==2">
        <Select
          size="default"
          v-model="row.BoxCode"
          filterable
          :transfer="true"
          :label-in-value="true"
          @on-change="changConveyingplace2($event,row)"
        >
          <Option
            v-for="(item,index) in Boxingarr"
            :value="item.Code"
            :label="item.Code"
            :key="index"
          >
            <span>{{item.Code}}</span>
            <span style="float:right;color:#ccc">0</span>
          </Option>
        </Select>
      </template>
      <template slot-scope="{ row, index }" slot="imglist">
        <p v-for="(item,index) in row.Files">
          <span>{{item.CustomName}}</span>
          <Icon type="ios-trash-outline" :ref="row.ID" @click.native="handleRemovelist(row,index)"></Icon>
        </p>
        <!-- <Input v-model="row.typeimg" /> -->
      </template>
      <template slot-scope="{ row, index }" slot="operation">
        <!-- <div class="setupload">
                    <Button type="primary" size="small" icon="ios-cloud-upload" @click="SeletUpload(row.ID)">传照</Button>
                </div>
                <div class="setupload">
                    <Button type="primary" size="small" icon="md-reverse-camera" @click="fromphotos(row.ID)">拍照</Button>
        </div>-->
      </template>
    </Table>
  </div>
</template>
<script>
import { GetSortingID } from "../../api/CgApi";
import { getWayParter } from "../../api/index";
export default {
  props: ["Storagesarr", "Storehouselistarr",'Boxingarr','status'],
  //   watch: {
  //         Storagesarr: function(newValue) {
  //             console.log(newValue);  //可以看到数据已经拿到
  //         },
  //     },
  data() {
    return {
      // Storehouselist: this.Storehouselistarr,
      Storehouselist:[],
      Conveyingplace: [],
      Storages: this.Storagesarr,
      titlename:"",
      columns7: [
        {
          title: "",
          slot: "indexs",
          width: 30,
          align: "center"
        },
        {
          title: " ",
          slot: "action",
          width: 100,
          align: "center"
        },
        {
          title: "型号",
          key: "PartNumber",
          render: (h, params) => {
            var vm = this;
            return h("Input", {
              props: {
                //将单元格的值给Input
                value: params.row.Product.PartNumber
              },
              on: {
                "on-change"(event) {
                  //值改变时
                  //将渲染后的值重新赋值给单元格值
                  params.row.Product.PartNumber = event.target.value;
                  vm.Storages[params.index] = params.row;
                  //   vm.consoleStorages()
                }
              }
            });
          }
        },
        {
          title: "品牌",
          key: "Manufacturer",
          render: (h, params) => {
            var vm = this;
            return h("Input", {
              props: {
                //将单元格的值给Input
                value: params.row.Product.Manufacturer
              },
              on: {
                "on-change"(event) {
                  //值改变时
                  //将渲染后的值重新赋值给单元格值
                  params.row.Product.Manufacturer = event.target.value;
                  vm.Storages[params.index] = params.row;
                  //   vm.consoleStorages()
                }
              }
            });
          }
        },
        {
          title: "批号",
          key: "DateCode",
          render: (h, params) => {
            var vm = this;
            return h("Input", {
              props: {
                //将单元格的值给Input
                value: params.row.DateCode
              },
              on: {
                "on-change"(event) {
                  //值改变时
                  //将渲染后的值重新赋值给单元格值
                  params.row.DateCode = event.target.value;
                  vm.Storages[params.index] = params.row;
                }
              }
            });
          }
        },
        {
          title: "到货数量",
          key: "Quantity",
          render: (h, params) => {
            var vm = this;
            return h("Input", {
              props: {
                //将单元格的值给Input
                value: params.row.CurrentQuantity
              },
              on: {
                "on-change"(event) {
                  //值改变时
                  //将渲染后的值重新赋值给单元格值
                  if (event.target.value != "") {
                    var reg = /^\d+(\.\d{0,2})?$/;
                    if (reg.test(event.target.value) == true) {
                      params.row.CurrentQuantity = event.target.value;
                      vm.Storages[params.index] = params.row;
                    } else {
                      vm.$Message.error("只能输入数量");
                      (event.target.value = ""),
                        (params.row.CurrentQuantity = "");
                      vm.Storages[params.index] = params.row;
                    }
                  }
                }
              }
            });
          }
        },
        {
          title: "原产地",
          slot: "Place"
        },
        {
          title: this.titlename,
          slot: "ShelveID",
          align: "left"
        },
        {
          title: "体积(cm³)",
          key: "Volume",
          align: "center",
          // width: 70,
          render: (h, params) => {
            var vm = this;
            return h("Input", {
              props: {
                //将单元格的值给Input
                value: params.row.Volume
              },
              on: {
                "on-change"(event) {
                  //值改变时
                  //将渲染后的值重新赋值给单元格值
                  var reg = /^\d+(\.\d{0,2})?$/;
                  // reg.test(event.target.value);
                  if (reg.test(event.target.value) == true) {
                    params.row.Volume = event.target.value;
                    vm.Storages[params.index] = params.row;
                  } else {
                    //   vm.$Message.error("只能输入数字,包括两位数的小数点");
                    params.row.Volume = "";
                    event.target.value = "";
                    vm.Storages[params.index] = params.row;
                  }
                },
                "on-blur"() {
                  //值改变时
                  //将渲染后的值重新赋值给单元格值
                  var reg = /^\d+(\.\d{0,2})?$/;
                  // reg.test(event.target.value);
                  if (reg.test(event.target.value) == true) {
                    params.row.Volume = event.target.value;
                    vm.Storages[params.index] = params.row;
                  } else {
                    vm.$Message.error("只能输入数字,包括两位数的小数点");
                    params.row.Volume = "";
                    event.target.value = "";
                    vm.Storages[params.index] = params.row;
                  }
                }
              }
            });
          }
        },
        {
          title: "毛重(g)",
          key: "Weight",
          align: "center",
          // width: 60,
          render: (h, params) => {
            var vm = this;
            return h("Input", {
              props: {
                //将单元格的值给Input
                value: params.row.Weight,
                autofocus: true
              },
              on: {
                "on-change"(event) {
                  var reg = /^\d+(\.\d{0,2})?$/;
                  var newtarget = vm.trim(event.target.value);
                  if (newtarget != "") {
                    if (reg.test(newtarget) == true) {
                      params.row.Weight = newtarget;
                      vm.Storages[params.index] = params.row;
                    } else {
                      // vm.$Message.error("只能输入数字,包括两位数的小数点");
                      params.row.Weight = "";
                      event.target.value = "";
                      vm.Storages[params.index] = params.row;
                    }
                  }
                },
                "on-blur"(event) {
                  var reg = /^\d+(\.\d{0,2})?$/;
                  var newtarget = vm.trim(event.target.value);
                  if (newtarget != "") {
                    if (reg.test(newtarget) == true) {
                      params.row.Weight = newtarget;
                      vm.Storages[params.index] = params.row;
                    } else {
                      vm.$Message.error("只能输入数字,包括两位数的小数点");
                      params.row.Weight = "";
                      event.target.value = "";
                      vm.Storages[params.index] = params.row;
                    }
                  }
                },
                "on-enter": event => {
                  var reg = /^\d+(\.\d{0,2})?$/;
                  // console.log(event.target.value)
                  var newtarget = vm.trim(event.target.value);
                  // console.log(newtarget)
                  if (reg.test(newtarget) == true) {
                    params.row.Weight = newtarget;
                    vm.Storages[params.index] = params.row;
                    var Input = params.row.Input;
                    var StandardProducts = params.row.Product;
                    var data2 = {
                      Quantity: params.row.Quantity, //数量
                      inputsID: Input.ID, //id
                      Catalog: StandardProducts.Catalog, //品名
                      PartNumber: StandardProducts.PartNumber, //型号
                      Manufacturer: StandardProducts.Manufacturer, //品牌
                      Packing: StandardProducts.Packing, //包装
                      PackageCase: StandardProducts.PackageCase, //封装
                      origin: Input.Origin
                    };
                    var newdata = [];
                    newdata.push(data2);
                    var configs = GetPrinterDictionary();
                    var getsetting = configs["产品标签"];
                    // var href=window.location.protocol+"//"+window.location.host;
                    // var newurl="http://hv.warehouse.b1b.com"+getsetting.Url;
                    // getsetting.Url=newurl;
                    var str = getsetting.Url;
                    var testurl = str.indexOf("http") != -1;
                    if (testurl == true) {
                      getsetting.Url = getsetting.Url;
                    } else {
                      getsetting.Url = this.printurl + getsetting.Url;
                    }
                    var data = {
                      setting: getsetting,
                      data: newdata
                    };
                    alert(JSON.stringify(data));
                    TemplatePrint(data);
                  } else {
                    vm.$Message.error("只能输入数字,包括两位数的小数点");
                    params.row.Weight = "";
                    event.target.value = "";
                    vm.Storages[params.index] = params.row;
                  }
                }
              }
            });
          }
        },
        {
          title: "图片",
          slot: "imglist",
          align: "center",
          width: 180
        },
        {
          title: "备注",
          key: "imglist",
          align: "center",
          render: (h, params) => {
            var vm = this;
            return h("Input", {
              props: {
                //将单元格的值给Input
                value: params.row.Summary
              },
              on: {
                "on-change"(event) {
                  //值改变时
                  //将渲染后的值重新赋值给单元格值
                  params.row.Summary = event.target.value;
                  vm.Storages[params.index] = params.row;
                }
              }
            });
          }
        },
        {
          title: "操作",
          slot: "operation",
          align: "center"
        }
      ]
    };
  },
  created() {
    
  },
  computed: {
  },
  mounted() {
    this.WayParterdata();
    console.log(this.Boxingarr)
    if(this.status==1){
      this.titlename="库位"
    }else{
      this.titlename="箱号"
    }
  },
  methods: {
    trim(str) {
      //去除前后空格
      return str.replace(/(^\s*)|(\s*$)/g, "");
    },
    WayParterdata() {
      //输送地列表
      getWayParter().then(res => {
        this.Conveyingplace = res.obj;
      });
    },
    delrow(index) {
      //删除选中的录入信息
      this.Storages.splice(index, 1);
    },
    addrow(index) {
      GetSortingID().then(res => {
        var newrow = {
          iscx: false,
          isenter: true,
          ID: "LR" + index + new Date().getTime(),
          NoticeID: null,
          Product: {
            PartNumber: "",
            Manufacturer: "",
            PackageCase: "",
            Packaging: ""
          },
          Conditions:{
            DevanningCheck: false,
            Weigh: false,
            CheckNumber: false,
            OnlineDetection: false,
            AttachLabel: false,
            PaintLabel: false,
            Repacking: false,
            IsCCC: false,
            IsCIQ: false,
            IsEmbargo: false,
            IsHighPrice: false,
            IsDeclared: false,
          },
          WaybillID: "",
          InputID: null,
          DateCode: "",
          Quantity: "",
          ArrivedQuantity: "",
          LeftQuantity: "",
          CurrentQuantity: "",
          Source: "",
          BoxCode: "",
          Weight:0,
          NetWeight: 0,
          Volume:0,
          ShelveID: "",
          Type: "",
          origintext: "",
          origin:"",
          originDes: "",
          Summary: "",
          SortingID: res,
          Files: [],
          _disabled: false
        };
        this.Storages.push(newrow);
      });
    },
    consoleStorages() {
      this.$parent.consoleStorages();
      // console.log(this.Storages)
    },
    changConveyingplace(value, row) {
      //改变库位号
      this.Storages.forEach(item => {
        if (item.ID == row.ID) {
          item.ShelveID = value.value;
          console.log(this.Storages);
        }
      });
    },
    changConveyingplace2(value, row) {
      //改变库位号
      this.Storages.forEach(item => {
        if (item.ID == row.ID) {
          item.BoxCode = value.value;
        }
      });
    },
    changoriginID(value, row) {
      console.log(value);
      this.Storages.forEach(item => {
        if (item.ID == row.ID) {
          item.origin = value.value;
          item.origintext = value.label;
          item.originDes = value.label;
          // console.log(this.Storages)
        }
      });
    }
  }
};
</script>


